import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';
import DateInput from '../components/DateInput';
import confirmAction from '../utils/confirmAction';

export default class TeacherForm extends Component {
	defaults = {
		admission_no: '',
		amount: '',
		date: '',
		method: ''
	};

	state = {error: {}, loading: false, data: {...this.defaults}};

	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}
	
	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Fee Payment Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Fee Payment Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error: error.response.data});
			return Promise.reject(error.response.data);
		}).finally(_=>this.setState({loading:false}));
	}

	delete = () => {
    confirmAction( () => {
      return axios.delete(`/api/finances/fee-payment/${this.props.data.id}`).then(res=>{
        this.props.close();
        return {success: 'Deleted!', succes_text: 'Fee payment was deleted'};
      }, error => {
        throw {error: "Fee payment couldn't be deleted."};
      })
    }, {loading_title: 'Deleting...', loading_text: 'Deleting Fee Payment', canceled: "Deleting Fee Payment Operation Cancelled", consequence: 'Fee Payment will be deleted'});
  }

	render() {
		let error = this.state.error || {};
		let admissionNoInvalid='', amountInvalid='', dateInvalid='', methodInvalid='';
		if(error.errors) {
			admissionNoInvalid = error.errors.admission_no ? 'is-invalid' : '';
			amountInvalid = error.errors.amount ? 'is-invalid' : '';
			dateInvalid = error.errors.date ? 'is-invalid' : '';
			methodInvalid = error.errors.method ? 'is-invalid' : '';
		}

		return <div className="container-fluid" >
			{this.props.edit && <div className="row">
          <div className="col-md-12">
            <span className="pull-right">
              <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
            </span>
          </div>
      </div>}
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Payment Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					      <div className="form-group">
					        <label>Admission Number</label>
					        <input value={this.state.data.admission_no} onChange={this.inputChanged.bind(this,'admission_no')} type="text" placeholder="Student Admission Number" className={`form-control ${admissionNoInvalid}`} />
					        {admissionNoInvalid && error.errors.admission_no.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="form-group">
					        <label>Amount</label>
					        <input value={this.state.data.amount} onChange={this.inputChanged.bind(this,'amount')} type="text" placeholder="Amount" className={`form-control ${amountInvalid}`} />
					        {amountInvalid && error.errors.amount.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="form-group">
					        <label>Method of Payment</label>
					        <select value={this.state.data.method} onChange={this.inputChanged.bind(this,'method')} name="method" className={`form-control ${methodInvalid}`} >
					        	<option value="">Select Method</option>
                    <option value="cash">CASH</option>
                    <option value="mobile">MOBILE</option>
                    <option value="cheque">CHEQUE</option>
                    <option value="bankers_cheque">BANKERS CHEQUE</option>
                  </select>
					        {methodInvalid && error.errors.method.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
					      </div>

					      <div className="form-group">
					        <label>Date</label>
					        <DateInput value={this.state.data.date} onChange={this.inputChanged.bind(this,'date')} placeholder="Date" className={`form-control ${dateInvalid}`} />
					        {dateInvalid && error.errors.date.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
						  	{this.props.edit ? <div className="col-sm-2">
			            	<input onClick={this.apply} type="button" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
			          	</div> :
			          	<div className="col-sm-3">
			            	<input onClick={this.saveContinue} type="button" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
			          	</div>
			          }
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}