import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

import DebtForm from '../components/DebtForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

export default class Debt extends Component {
    constructor(props) {
        super(props);
        window.todListActionEdit = this.edit;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get('/api/finances/debt', {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'debtor', name: 'debtor', 'class': 'text-center'},
                { data: 'amount', name: 'amount', 'class': 'text-center'},
                { data: 'payed', name: 'payed', 'class': 'text-center', render: (data,type,row) => data ? data : '-' },
                { data: 'outstanding', name: 'outstanding', 'class': 'text-center', render: (data,type,row) => data ? data : '-' },
                { data: 'date', name: 'date', 'class': 'text-center', render: (data,type,row) => data ? (new Date(data)).toDateString() : '-' },
                { data: 'due', name: 'due', 'class': 'text-center', render: (data,type,row) => data ? (new Date(data)).toDateString() : '-' },
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="todListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
        };
    }

    edit = (debt) => {
        this.props.history.push(`${this.props.match.url}/${debt}`);
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/new`} className="btn btn-success details">New Debt</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Debts" config={this.dataTableConfig}>
                <th>Debtor</th>
                <th>Amount</th>
                <th>Payed</th>
                <th>Outstanding</th>
                <th>Date</th>
                <th>Due</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

export class DebtNew extends Component {
    state = { error: {} };
    submit = (data) => {
        return axios.post('/api/finances/debt', data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/finances/debt');
    }
    render() {
        return <DebtForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}

export class DebtEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
    if(this.props.location.state && this.props.location.state.debt){
        this.setState({data: this.props.location.state.debt})
    }else if(this.props.match.params.debtId){
        axios.get(`/api/finances/debt/${this.props.match.params.debtId}`).then(response => {
            this.setState({data: response.data});
        },error=>this.setState({error:error.response.data}))
    }
    
  }

  submit = (data) => {
        return axios.put(`/api/finances/debt/${this.props.match.params.debtId}`, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/finances/debt');
    }

    render() {
        return <DebtForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
    }
}