import React, { Component } from 'react';
import $ from 'jquery';

export default class Checkbox extends Component {
	inputEl = React.createRef();
	constructor(props) {
		super(props);

		this.state = {checked: this.props.checked};
	}
	inputChanged = (event) => {
		event.stopPropagation();
		if(this.props.onChange)
			this.props.onChange(event);
	}

	componentDidUpdate(prevProps,prevState) {
		if(this.props.checked != prevProps.checked) {
			this.setState({checked: this.props.checked})
		}
	}

	toggle = (event) => {
		if(this.props.disabled){
			return;
		}
		let value = this.state.checked ? false : true;
		this.setState({checked: value}, ()=>{
			var nativeInputValueSetter = Object.getOwnPropertyDescriptor(window.HTMLInputElement.prototype, "value").set;
			nativeInputValueSetter.call(this.inputEl.current, 'on');

			var ev2 = new Event('click', { bubbles: true});
			this.inputEl.current.dispatchEvent(ev2);
		});
	}

	render() {
		return <div className={`bootstrap-switch-${this.state.checked ? 'on' : 'off'} bootstrap-switch bootstrap-switch-wrapper bootstrap-switch-animate`}
			style={{width: '72px'}} onClick={this.toggle}>
    	<div className="bootstrap-switch-container" style={{width: '122px', marginLeft: '0px'}}>
    		<span className="bootstrap-switch-handle-on bootstrap-switch-info" style={{width: '50px'}}>
    			<i className="fa fa-check"></i>
    		</span>
    		<span className="bootstrap-switch-label" style={{width: '30px'}} >&nbsp;</span>
    		<span className="bootstrap-switch-handle-off bootstrap-switch-info" style={{width: '50px'}}>
    			<i className="fa fa-times"></i>
    		</span>
    		<input ref={this.inputEl} checked={this.state.checked} onChange={()=>{}}  onClick={this.inputChanged} type="checkbox" name={this.props.name} data-toggle="switch" data-on-color="info" data-off-color="info" data-on-text="<i class='fa fa-check'></i>" data-off-text="<i class='fa fa-times'></i>" />
    		
    	</div>
    </div>
	}
}
