import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import {inject} from 'mobx-react';

import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

import FeeCategoryForm from '../components/FeeCategoryForm';

@inject('classesStore')
export default class FeeStructure extends Component {
  state = { date: '', classes: null, class_id: '' };
  dataTable = null;
  constructor(props) {
    super(props);
    var self = this;
    window.feeCategoryActionEdit = this.edit;

    this.state = { date: '', classes: null, class_id: '', total: 0, totalCommon: 0, totalClass: 0 };
    if(this.props.location.state && this.props.location.state.class_id)
      this.state.class_id = this.props.location.state.class_id;

    this.dataTable = React.createRef()
    this.dataTableConfig = {
      dom: 'lfrtip',
      paging: false,
      searching: false,
      serverSide: true,
      processing: true,
      ajax: function(params, callback, settings) {
        params.class_id = self.state.class_id || '';
        axios.get('/api/finances/fee-structure', {params: {params}}).then(response=>{
          callback(response.data);
          self.setState({
            total: response.data.total,
            totalCommon: response.data.totalCommon,
            totalClass: response.data.totalClass ? response.data.totalClass : 0
          });
        })
      },
      columns: [
        { data: 'name', name: 'name', 'class': 'text-center'},
        { data: 'amount', name: 'amount', 'class': 'text-center'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
            <a onclick="feeCategoryActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
          </span>`
        }}
      ],
    };
  }

  edit = (category) => {
    this.props.history.push(`${this.props.match.url}/category/${category}`);
  }

  inputChanged = (event) => {
    this.setState({class_id: event.target.value}, this.filterChanged);
  }

  filterChanged = () => {
    this.dataTable.current.draw();
  }

	componentDidMount() {
    this.props.classesStore.list({},true).then(classes=>{
      this.setState({classes});
    });
	}

	render() {
    let match = this.props.match;
		let {classes} = this.state;

		return <div className="container-fluid">
      <div className="row">
        <div className="col-md-12">
          <span className="pull-right">
            <Link to={`${match.url}/category/new${this.state.class_id ? ('/' + this.state.class_id ) : '' }`} className="btn btn-dark details ml-1">Add Fee Category</Link>
          </span>
        </div>
      </div>
      <DataTable ref={this.dataTable} title="Fee Structure" config={this.dataTableConfig} toolbar={
        <>
          <div className="col-6">
            <div className="form-group row">
              <label className="col-sm-2 col-form-label">Class:</label>
              <div className="col-10">
                <select onChange={this.inputChanged} value={this.state.class_id} id='filterByClass' className="form-control">
                  <option value=''> Common </option>
                  {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
                </select>
              </div>
            </div>
          </div>
          <div className="col-6">
            <div className="row">
              <div className="col-4">
                <p><span>Total:</span> {this.state.total}</p>
              </div>
              <div className="col-4">
                <p><span>Common:</span> {this.state.totalCommon}</p>
              </div>
              <div className="col-4">
                <p><span>Class:</span> {this.state.totalClass}</p>
              </div>
            </div>
          </div>
        </>
      }>
        <th>Category</th>
        <th>Amount</th>
        <th>Action</th>
      </DataTable>
		</div>

	}
}

export class FeeCategoryNew extends Component {
    state = { error: {} };
    submit = (data) => {
        if(this.props.match.params.classId) {
          data.class_id = this.props.match.params.classId;
        }
        return axios.post('/api/finances/fee-structure/category/new', data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/finances/fee-structure', {class_id: this.props.match.params.classId});
    }
    render() {
        return <FeeCategoryForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}

export class FeeCategoryEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
        if(this.props.location.state && this.props.location.state.feeCategory){
            this.setState({data: this.props.location.state.feeCategory});
        }else if(this.props.match.params.categoryId){
            axios.get(`/api/finances/fee-structure/category/${this.props.match.params.categoryId}`).then(response => {
                this.setState({data: response.data});
            },error=>this.setState({error:error.response.data}))
        }
        
    }

  submit = (data) => {
        return axios.put(`/api/finances/fee-structure/category/${this.props.match.params.categoryId}`, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/finances/fee-structure', {class_id: this.state.data.class_id});
    }

    render() {
        return <FeeCategoryForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
    }
}
