import React, {Component} from 'react';
import lodash from 'lodash';

import CardStats from '../components/CardStats';
import Chartist from 'chartist';

export default class Finance extends Component {
	state = {finances: null }
	componentDidMount() {
		axios.get('/api/finances').then(res=>{
			this.setState({finances: res.data});
		})
		this.initChart();
	}

	componentDidUpdate(prevProps, prevState) {
		if(!lodash.isEqual(this.state.finances,prevState.finances)){
			this.initChart();
		}
	}

	initChart() {
		if(!this.state.finances)
			return;
		let finances = this.state.finances;
		var dataPreferences = {
      series: [
        [25, 30, 20, 25]
      ]
    };

    var optionsPreferences = {
      donut: true,
      donutWidth: 40,
      startAngle: 0,
      total: 100,
      showLabel: false,
      axisX: {
          showGrid: false
      }
    };
    Chartist.Pie('#chartEmail', dataPreferences, optionsPreferences);
    Chartist.Pie('#chartEmail', this.calculateChartStat(finances.fees, finances.payments, finances.debt));
	}

	calculateBalance(balance_down,payments,expenditure) {
		let b = balance_down ? balance_down.balance : 0;
		let p = payments ? payments.total : 0;
		let e = expenditure ? expenditure.total : 0;
		let date = balance_down ? balance_down.date : '';
		date = payments && (Date.parse(payments.date) > Date.parse(date) ? payments.date : date)
		date = expenditure && (Date.parse(expenditure.date) > Date.parse(date) ? expenditure.date : date)
		let balance = b + p - e;
		return {balance,date};
	}

	calculateChartStat(fees,payments,debt){
		payments = payments ? payments.total : 0;
		debt = debt ? debt.outstanding : 0;
		let unpayed = (fees ? fees.total : 0) - payments;
		if(unpayed >= 0) {
			let total = payments + unpayed + debt;
			payments = (payments/total) * 100;
			unpayed = (unpayed/total) * 100;
			debt = (debt/total) * 100;
			return {
				labels: [`${Math.floor(payments)}%`, `${Math.floor(debt)}%`, `${Math.floor(unpayed)}%`],
				series: [payments, debt, unpayed]
			};
		} else {
			let total = payments + debt;
			payments = (payments/total) * 100;
			debt = (debt/total) * 100;
			return {
				labels: [`${Math.floor(payments)}%`, `${Math.floor(debt)}%`],
				series: [payments, debt]
			};
		}
	}

	render() {
		let finances = this.state.finances || {};
		let balance = this.calculateBalance(finances.balance_down,finances.payments,finances.expenditure);
		return <div className="container-fluid">
			<div className="row">
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Balance Carried Down"
						title={finances.balance_down ? finances.balance_down.balance : 0}
						icon="nc-cloud-download-93"
						footer={finances.balance_down ? (new Date(finances.balance_down.date)).toDateString() : '-' } />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Total Fees"
						title={finances.fees ? finances.fees.total : 0}
						icon="nc-bag"
						footer={finances.fees ? (new Date(finances.fees.date)).toDateString() : '-' } />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats 
						category="Total Debt"
						title={finances.debt ? finances.debt.outstanding : 0}
						icon="nc-bank"
						footer={finances.debt ? (new Date(finances.debt.date)).toDateString() : '-' } />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Total Payment"
						title={finances.payments ? finances.payments.total : 0}
						icon="nc-money-coins"
						footer={finances.payments ? (new Date(finances.payments.date)).toDateString() : '-' } />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Total Expenditure"
						title={finances.expenditure ? finances.expenditure.total : 0}
						icon="nc-paper-2"
						footer={finances.expenditure ? (new Date(finances.expenditure.date)).toDateString() : '-'} />
				</div>
				<div className="col-lg-4 col-sm-6">
					<CardStats
						category="Account Balance"
						title={balance ? balance.balance : 0}
						icon="nc-credit-card"
						footer={balance ? (new Date(balance.date)).toDateString() : '-'} />
				</div>
			</div>

			<div className="row">
        <div className="col-md-4">
            <div className="card ">
                <div className="card-header ">
                    <h4 className="card-title">Period Statistics</h4>
                    <p className="card-category">Current Period Status</p>
                </div>
                <div className="card-body ">
                    <div id="chartEmail" className="ct-chart ct-perfect-fourth"></div>
                </div>
                <div className="card-footer ">
                    <div className="legend">
                        <i className="fa fa-circle text-info"></i> Payed Fees
                        <i className="fa fa-circle text-danger"></i> Debt
                        <i className="fa fa-circle text-warning"></i> Unpayed fees
                    </div>
                    <hr />
                    <div className="stats">
                        <i className="fa fa-clock-o"></i> 
                    </div>
                </div>
            </div>
        </div>
      </div>

		</div>
	}
}