import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

import ExpenditureForm from '../components/ExpenditureForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

export default class Expenditure extends Component {
    constructor(props) {
        super(props);
        window.expenditureListActionEdit = this.edit;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get('/api/finances/expenditure', {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'date', name: 'date', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
                { data: 'amount', name: 'amount', 'class': 'text-center'},
                { data: 'expensable_type', name: 'expensable_type', 'class': 'text-center', render: (data,type,row) => (data||'MISCELLANEOUS').toUpperCase() },
                { data: 'cause', name: 'cause', 'class': 'text-center'},
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="expenditureListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
        };
    }

    edit = (expenditure) => {
        this.props.history.push(`${this.props.match.url}/${expenditure}`);
    }
    
	render() {
		let match = this.props.match;
		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/new`} className="btn btn-success details">New Expenditure</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Expenditures" config={this.dataTableConfig}>
                <th>Date</th>
                <th>Amount</th>
                <th>Type</th>
                <th>Cause</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

export class ExpenditureNew extends Component {
    state = { error: {} };
    submit = (data) => {
        return axios.post('/api/finances/expenditure', data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/finances/expenditure');
    }
    render() {
        return <ExpenditureForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}

export class ExpenditureEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
    if(this.props.location.state && this.props.location.state.expenditure){
        this.setState({data: this.props.location.state.expenditure})
    }else if(this.props.match.params.expenditureId){
        axios.get(`/api/finances/expenditure/${this.props.match.params.expenditureId}`).then(response => {
            this.setState({data: response.data.data});
        },error=>this.setState({error:error.response.data}))
    }
    
  }

  submit = (data) => {
        return axios.put(`/api/finances/expenditure/${this.props.match.params.expenditureId}`, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/finances/expenditure');
    }

    render() {
        return <ExpenditureForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
    }
}