import React, { Component } from 'react';
import { Route } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import FeeStructure, { FeeCategoryNew, FeeCategoryEdit } from './FeeStructure';
import FeePayment, { FeePaymentNew, FeePaymentEdit } from './FeePayment';
import FeeBalance from './FeeBalance';
import Debt, { DebtNew, DebtEdit } from './Debt';
import Expenditure, { ExpenditureNew, ExpenditureEdit } from './Expenditure';
import Finance from './Finance';

export default class FinancePages extends Component {
	render() {
		let match = this.props.match;
		return <AnimatedSwitch>
						<Route path={`${match.path}/fee-structure/category/new/:classId?`} render={(props) => <FeeCategoryNew {...props} /> } />
						<Route path={`${match.path}/fee-structure/category/:categoryId`} render={(props) => <FeeCategoryEdit {...props} /> } />
						<Route exact path={`${match.path}/fee-structure`} render={(props) => <FeeStructure {...props} /> } />

						<Route path={`${match.path}/fee-payment/new`} render={(props) => <FeePaymentNew {...props} /> } />
						<Route path={`${match.path}/fee-payment/:paymentId`} render={(props) => <FeePaymentEdit {...props} /> } />
						<Route exact path={`${match.path}/fee-payment`} render={(props) => <FeePayment {...props} /> } />

						<Route exact path={`${match.path}/fee-balance`} render={(props) => <FeeBalance {...props} /> } />

						<Route path={`${match.path}/debt/new`} render={(props) => <DebtNew {...props} /> } />
						<Route path={`${match.path}/debt/:debtId`} render={(props) => <DebtEdit {...props} /> } />
						<Route exact path={`${match.path}/debt`} render={(props) => <Debt {...props} /> } />

						<Route path={`${match.path}/expenditure/new`} render={(props) => <ExpenditureNew {...props} /> } />
						<Route path={`${match.path}/expenditure/:expenditureId`} render={(props) => <ExpenditureEdit {...props} /> } />
						<Route exact path={`${match.path}/expenditure`} render={(props) => <Expenditure {...props} /> } />

						<Route path="" render={(props) => <Finance {...props} /> } />
					</AnimatedSwitch>
	}
}
