import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

import FeePaymentForm from '../components/FeePaymentForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

export default class FeePayment extends Component {
    constructor(props) {
        super(props);
        window.todListActionEdit = this.edit;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get('/api/finances/fee-payment', {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'admission_no', name: 'admission_no', 'class': 'text-center'},
                { data: 'firstname', name: 'firstname', 'class': 'text-center'},
                { data: 'lastname', name: 'lastname', 'class': 'text-center'},
                { data: 'amount', name: 'amount', 'class': 'text-center'},
                { data: 'method', name: 'method', 'class': 'text-center'},
                { data: 'date', name: 'date', 'class': 'text-center', render: (data,type,row) => (new Date(data)).toDateString() },
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="todListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
        };
    }

    edit = (payment) => {
        this.props.history.push(`${this.props.match.url}/${payment}`);
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/new`} className="btn btn-success details">New Payment</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Fee Payment" config={this.dataTableConfig}>
                <th>Admission No.</th>
                <th>Firstname</th>
                <th>Lastname</th>
                <th>Amount</th>
                <th>Method</th>
                <th>Date</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

export class FeePaymentNew extends Component {
    state = { error: {} };
    submit = (data) => {
        return axios.post('/api/finances/fee-payment', data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/finances/fee-payment');
    }
    render() {
        return <FeePaymentForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}

export class FeePaymentEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
    if(this.props.location.state && this.props.location.state.payment){
        this.setState({data: this.props.location.state.payment})
    }else if(this.props.match.params.paymentId){
        axios.get(`/api/finances/fee-payment/${this.props.match.params.paymentId}`).then(response => {
            this.setState({data: response.data});
        },error=>this.setState({error:error.response.data}))
    }
    
  }

  submit = (data) => {
        return axios.put(`/api/finances/fee-payment/${this.props.match.params.paymentId}`, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/finances/fee-payment');
    }

    render() {
        return <FeePaymentForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
    }
}