import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import {inject} from 'mobx-react';

import StudentForm from '../components/StudentForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

@inject('classesStore')
export default class FeeBalance extends Component {
  dateRef = React.createRef();
  state = { classes: null, class_id: '', stream_id: '', date: '' };
  dataTable = null;
  constructor(props) {
    super(props);
    var self = this;
    this.dataTable = React.createRef()
    this.dataTableConfig = {
      serverSide: true,
      processing: true,
      ajax: function(params, callback, settings) {
        params.class_id = self.state.class_id;
        params.stream_id = self.state.stream_id;
        axios.get('/api/finances/fee-balance', {params: {params}}).then(response=>{
          callback(response.data);
        })
      },
      columns: [
        { data: 'admission_no', name: 'admission_no', 'class': 'text-center'},
        { data: 'firstname', name: 'firstname', 'class': 'text-center'},
        { data: 'lastname', name: 'lastname', 'class': 'text-center'},
        { data: 'total', name: 'total', 'class': 'text-center', render: (data,type,row) => data ? data : '-' },
        { data: 'payed', name: 'payed', 'class': 'text-center', render: (data,type,row) => data ? data : '-' },
        { data: 'balance', name: 'balance', 'class': 'text-center', render: (data,type,row) => data ? data : '-' }
      ],
    };
  }

  inputChanged(field,event) {
    let val = event.target.value;
    if(field == 'stream_id')
      this.setState({stream_id: val}, this.filterChanged);
    else if(field == 'class_id')
      this.setState({class_id: val, stream_id: ''}, this.filterChanged);
    else
      this.setState({[field]: val}, this.filterChanged);
  }

  filterChanged = () => {
    this.dataTable.current.draw();
  }

	componentDidMount() {
    this.props.classesStore.list({},true).then(classes=>{
      this.setState({classes});
    })
	}

	render() {
		let match = this.props.match;

    let {classes} = this.state;
    let streams = null;

    if(classes) {
      let selectedClass = classes.find( (cls) => cls.id == this.state.class_id );
      if(selectedClass) {
        streams = selectedClass.streams;
      }
    }

		return <div className="container-fluid">
      <DataTable ref={this.dataTable} title="Fee Balance" config={this.dataTableConfig} toolbar={
        <>
          <select onChange={this.inputChanged.bind(this,'class_id')} value={this.state.class_id} id='filterByClass' className="form-control col-6">
            <option value=''>-- Select Class --</option>
            {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
          </select>
          <select onChange={this.inputChanged.bind(this,'stream_id')} value={this.state.stream_id} id='filterByStream' className="form-control col-6">
            <option value=''>-- Select Stream --</option>
            {streams && streams.map( (stream) => <option key={stream.id} value={stream.id}>{stream.name}</option> )}
          </select>
        </>
      }>
        <th>Admission No.</th>
        <th>Firstname</th>
        <th>Lastname</th>
        <th>Total</th>
        <th>Payed</th>
        <th>Balance</th>
      </DataTable>
		</div>

	}
}
