import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';
import DateInput from '../components/DateInput';
import confirmAction from '../utils/confirmAction';

export default class ExpenditureForm extends Component {
	defaults = {
		date: '',
		amount: '',
		expensable_type: '',
		cause: '',
		debt_id: '',
		stock_item_id: '',
		quantity: ''
	};

	state = {error: {}, loading: false, data: {...this.defaults}};

	componentDidMount() {
    axios.get('/api/finances/debt').then(res=>{
    	this.setState({debts: res.data.data});
    });

    axios.get('/api/stocks').then(res=>{
    	this.setState({stockItems: res.data});
    })

    axios.get('/api/staffs').then(res=>{
    	this.setState({staffs: res.data.data});
    })
	}

	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (event) => {
		let field = event.target.name;
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}
	
	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Expenditure Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Expenditure Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error: error.response.data});
			return Promise.reject(error.response.data);
		}).finally(_=>this.setState({loading:false}));
	}

	delete = () => {
    confirmAction( () => {
      return axios.delete(`/api/finances/expenditure/${this.props.data.id}`).then(res=>{
        this.props.close();
        return {success: 'Deleted!', succes_text: 'Expenditure was deleted'};
      }, error => {
        throw {error: "Expenditure couldn't be deleted."};
      })
    }, {loading_title: 'Deleting...', loading_text: 'Deleting Expenditure', canceled: "Deleting Expenditure Operation Cancelled", consequence: 'Expenditure will be deleted'});
  }

	render() {
		let error = this.state.error || {};
		let dateInvalid='', amountInvalid='', typeInvalid='', causeInvalid='', debtorInvalid='', staffInvalid='', stockItemInvalid='', stockQuantityInvalid='';
		if(error.errors) {
			dateInvalid = error.errors.date ? 'is-invalid' : '';
			amountInvalid = error.errors.amount ? 'is-invalid' : '';
			typeInvalid = error.errors.expensable_type ? 'is-invalid' : '';
			causeInvalid = error.errors.cause ? 'is-invalid' : '';
			debtorInvalid = error.errors.debt_id ? 'is-invalid' : '';
			staffInvalid = error.errors.staff_id ? 'is-invalid' : '';
			stockItemInvalid = error.errors.stock_item_id ? 'is-invalid' : '';
			stockQuantityInvalid = error.errors.quantity ? 'is-invalid' : '';
		}

		let {debts, stockItems, staffs} = this.state;

		return <div className="container-fluid" ref={this.elRef} >
			{this.props.edit && <div className="row">
          <div className="col-md-12">
            <span className="pull-right">
              <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
            </span>
          </div>
      </div>}
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Expenditure Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="row">
					    		<div className="form-group col-sm-6">
						        <label>Date</label>
						        <DateInput value={this.state.data.date} onChange={this.inputChanged} name="date" type="text" placeholder="Date" className={`form-control ${dateInvalid}`} />
						        {dateInvalid && error.errors.date.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-sm-6">
						        <label>Amount</label>
						        <input value={this.state.data.amount} onChange={this.inputChanged} name="amount" type="text" placeholder="Amount" className={`form-control ${amountInvalid}`} />
						        {amountInvalid && error.errors.amount.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					    	</div>

					      <div className="row">
						      <div className="form-group col-sm-6">
						        <label>Expenditure Type</label>
						        <select value={this.state.data.expensable_type} onChange={this.inputChanged} name="expensable_type" className={`form-control ${typeInvalid}`} size="9" >
						        	<option value="">MISCELLANEOUS</option>
	                    <option value="debt">DEBT</option>
	                    <option value="stock">STOCK</option>
	                    <option value="staff">STAFF</option>
	                  </select>
						        {typeInvalid && error.errors.expensable_type.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>

						      <div className="col-sm-6">
						      	{this.state.data.expensable_type == 'debt' && <div className="form-group">
						      		<label>Debtor</label>
							        <select value={this.state.data.debt_id} onChange={this.inputChanged} name="debt_id" className={`form-control ${debtorInvalid}`} size="9" >
		                    <option value=""> --Select Debtor-- </option>
	                    	{debts && debts.map( debt => <option key={debt.id} value={debt.id}>{lodash.unescape(debt.debtor)}</option>)}
		                  </select>
							        {debtorInvalid && error.errors.debt_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
						      	</div>}

						      	{this.state.data.expensable_type == 'stock' && <div className="form-group">
						      		<label>Stock</label>
							        <select value={this.state.data.stock_item_id} onChange={this.inputChanged} name="stock_item_id" className={`form-control ${stockItemInvalid}`} size="5" >
		                    <option value=""> --Select Item-- </option>
	                    	{stockItems && stockItems.map( item => <option key={item.id} value={item.id}>{item.name}</option>)}
		                  </select>
		                  {stockItemInvalid && error.errors.stock_item_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
		                  &nbsp;
		                  <input type="text"
		                  	placeholder="Quantity"
		                  	value={this.state.data.quantity}
		                  	onChange={this.inputChanged}
		                  	name="quantity"
		                  	className={`form-control ${stockQuantityInvalid}`} />
							        {stockQuantityInvalid && error.errors.quantity.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
						      	</div>}

						      	{this.state.data.expensable_type == 'staff' && <div className="form-group">
						      		<label>Staff</label>
							        <select value={this.state.data.staff_id} onChange={this.inputChanged} name="staff_id" className={`form-control ${staffInvalid}`} size="9" >
		                    <option value=""> -- Select Staff Member -- </option>
	                    	{staffs && staffs.map( staff => <option key={staff.id} value={staff.id}>{staff.firstname} {staff.lastname}</option>)}
		                  </select>
							        {staffInvalid && error.errors.staff_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
						      	</div>}

						      	{this.state.data.expensable_type == '' && <div className="form-group">
						      		<label>Cause</label>
						      		<textarea
						      			value={this.state.data.cause}
						      			onChange={this.inputChanged} name="cause"
						      			placeholder="What was the expenditure for" 
						      			className={`form-control ${causeInvalid}`}>
						      		</textarea>
						      		{causeInvalid && error.errors.cause.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
						      	</div>}
						      </div>

						    </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
						  	{this.props.edit ? <div className="col-sm-2">
			            	<input onClick={this.apply} type="button" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
			          	</div> :
			          	<div className="col-sm-3">
			            	<input onClick={this.saveContinue} type="button" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
			          	</div>
			          }
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}